// App.vue
<template>
  <div class="container">
    <div class="image-converter">
      
      <div class="header-section">
        <h2>Image Converter</h2>
        <button class="help-btn" @click="showHelpModal = true">
          <span class="material-icons">help</span>
        </button>
      </div>

      <div class="upload-section">
        <input type="file" accept="image/*" @change="onFilesSelected" ref="fileInput" multiple>   

        <div class="upload-actions">  
          <label class="import-btn">
            <input  @click="$refs.fileInput.click()" class="file-input">
            <span class="material-icons">file_open</span>
            Select Images
          </label>
          <button class="clear-btn" @click="clearAllFiles" :disabled="selectedFiles.length === 0">
            <span class="material-icons">clear_all</span>
            Clear
          </button>
        </div> 

        <!-- <div v-if="selectedFiles.length > 0" class="selected-files"> -->
        <div class="selected-files">
          <p>Selected {{ selectedFiles.length }} files:</p>
          <ul>
            <li v-for="(file, index) in selectedFiles" :key="index" @click="loadImagePreview(file)">
              <div class="file-info">
                <span class="file-name">{{ file.name }}</span>
                <span class="file-size">{{ formatFileSize(file.size) }}</span>
              </div>
              <button class="delete-btn" @click.stop="removeFile(index)">
                <span class="material-icons">delete</span>
              </button>
            </li>
          </ul>
        </div>
      </div>

      <div class="preview-section" v-if="imagePreview">
        <h3>Preview</h3>
        <div class="image-preview">
          <div class="preview-container">
            <img class="checkerboard" :src="imagePreview" alt="Preview" :style="{ objectFit: 'contain' }" />
            <div class="image-info">
              {{ imageWidth }} X {{ imageHeight }}
            </div>
          </div>
        </div>
      </div>
      
      <div class="conversion-options" :class="{ 'disabled-section': selectedFiles.length === 0 }">
        <h3>Conversion Options</h3>
        <div class="format-selection">
          <div class="option-row">
            <div class="option-label">
              <h3>Color Format</h3>
            </div>
            <div class="option-control">
              <div class="select-wrapper">
                <select v-model="format" :disabled="selectedFiles.length === 0">
                  <optgroup label="Special Formats">
                    <option value="nvp">NVP (Alpha Channel)</option>
                  </optgroup>
                  <optgroup label="Black & White">
                    <option value="bw">Black & White (1-bit)</option>
                    <option value="bw4bit">Grayscale (4-bit)</option>
                    <option value="bw8bit">Grayscale (8-bit)</option>
                  </optgroup>
                  <optgroup label="32-bit Format (8888)">
                    <option value="rgba8888">RGBA8888</option>
                    <option value="argb8888">ARGB8888</option>
                    <option value="bgra8888">BGRA8888</option>
                    <option value="abgr8888">ABGR8888</option>
                  </optgroup>
                  <optgroup label="16-bit Format (4444)">
                    <option value="rgba4444">RGBA4444</option>
                    <option value="argb4444">ARGB4444</option>
                    <option value="bgra4444">BGRA4444</option>
                    <option value="abgr4444">ABGR4444</option>
                  </optgroup>
                  <optgroup label="16-bit Format (565)">
                    <option value="rgb565">RGB565</option>
                    <option value="bgr565">BGR565</option>
                  </optgroup>
                </select>
              </div>
              <div class="color-legend-item">
                <div class="color-legend" :class="getLegendClass">
                  <template v-for="channel in getChannelOrder" :key="channel">
                    <span :style="getChannelStyle(channel)">{{ channel }}</span>
                  </template>
                </div>
                <span class="legend-description">{{ getFormatDescription }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="output-selection">
          <div class="option-row">
            <div class="option-label">
              <h3>Output Format</h3>
            </div>
            <div class="option-control">
              <div class="select-wrapper">
                <select v-model="outputType" :disabled="selectedFiles.length === 0">
                  <option value="binary">Binary</option>
                  <option value="c-array">C Array</option>
                </select>
              </div>
            </div>
          </div>
        </div>

        <div v-if="format === 'bw'" class="threshold-selection">
          <div class="option-row">
            <div class="option-label">
              <h3>Threshold</h3>
            </div>
            <div class="option-control">
              <div class="slider-container">
                <input 
                  type="range" 
                  min="0" 
                  max="255" 
                  v-model.number="bwThreshold" 
                  :disabled="selectedFiles.length === 0"
                  class="threshold-slider"
                >
                <span class="threshold-value">{{ bwThreshold }}</span>
              </div>
              <span class="threshold-description">Pixels with brightness above this value will be white, below will be black</span>
            </div>
          </div>
        </div>

        <!-- NVP 변환 옵션 -->
        <div v-if="format === 'nvp'" class="nvp-options">
          <div class="option-row">
            <div class="option-label">
              <h3>Alpha Mode</h3>
            </div>
            <div class="option-control">
              <div class="select-wrapper">
                <select v-model="nvpAlphaMode" :disabled="selectedFiles.length === 0">
                  <option value="alpha">Use Alpha Channel</option>
                  <option value="luminance">Use Luminance</option>
                </select>
              </div>
              <span class="option-description">Alpha Channel: Use image's alpha channel. Luminance: Convert image brightness to alpha.</span>
            </div>
          </div>
          <div class="option-row" v-if="nvpAlphaMode === 'luminance'">
            <div class="option-label">
              <h3>Invert</h3>
            </div>
            <div class="option-control">
              <label class="checkbox-container">
                <input type="checkbox" v-model="nvpInvert" :disabled="selectedFiles.length === 0">
                <span>Invert luminance values</span>
              </label>
            </div>
          </div>
        </div>
        
        <div class="option-row" v-if="selectedFiles.length > 1">
          <div class="option-label">
            <h3>Output Options</h3>
          </div>
          <div class="option-control">
            <label class="checkbox-container">
              <input type="checkbox" v-model="zipOutput" :disabled="selectedFiles.length === 0">
              <span>Combine all outputs into a ZIP file</span>
            </label>
          </div>
        </div>
        
        <button class="import-btn" @click="convertAllImages" :disabled="selectedFiles.length === 0 || isConverting">
          <span class="material-icons">published_with_changes</span>
          {{ isConverting ? 'Converting...' : selectedFiles.length > 1 ? 'Convert All Images' : 'Convert Image' }}
        </button>
      </div>
      
      
      
      <div v-if="conversions.length > 0" class="conversion-results">
        <h3>Conversion Results</h3>
        <div class="results-list">
          <div v-for="(result, index) in conversions" :key="index" class="result-item">
            <div class="result-info">
              <strong>{{ result.fileName }}</strong>
              <span>{{ result.size }} KB</span>
              <span>Compression: {{ result.compressionRatio }}%</span>
            </div>
          </div>
        </div>
      </div>

      <div class="modal" v-if="showHelpModal">
        <div class="modal-content">
          <div class="modal-header">
            <h2>Image Converter User Guide</h2>
            <button class="close-btn" @click="showHelpModal = false">
              <span class="material-icons">close</span>
            </button>
          </div>
          <div class="modal-body">
            <p>This tool converts images into formats optimized for embedded devices.</p>

            <h3>Main Features</h3>
            <ol>
              <li>
                <strong>Manage Images</strong>
                <ul>
                  <li>Click the "Select Images" button to choose one or multiple image files.</li>
                  <li>New images will be added to the existing list without replacing previously selected images.</li>
                  <li>Click on an image in the list to display its preview.</li>
                  <li>Use the delete button (🗑️) next to each image to remove individual images from the list.</li>
                  <li>Use the "Clear" button to remove all selected images at once.</li>
                </ul>
              </li>
              <li>
                <strong>Set Conversion Options</strong>
                <ul>
                  <li>
                    <strong>Color Format</strong>: Choose the color format for the output image.
                    <ul>
                      <li><strong>NVP (Alpha Channel)</strong>: Extracts alpha channel information into a special format</li>
                      <li>Black & White (1-bit): Converts image to pure black and white using a threshold</li>
                      <li>32-bit Format (8888): RGBA8888, ARGB8888, BGRA8888, ABGR8888</li>
                      <li>16-bit Format (4444): RGBA4444, ARGB4444, BGRA4444, ABGR4444</li>
                      <li>16-bit Format (565): RGB565, BGR565</li>
                    </ul>
                  </li>
                  <li>
                    <strong>Output Format</strong>: Select the output file format.
                    <ul>
                      <li>Binary: Output as a binary file</li>
                      <li>C Array: Output as a C language array</li>
                    </ul>
                  </li>
                  <li>
                    <strong>Output Options</strong>: Activated when multiple images are selected.
                    <ul>
                      <li>"Combine all outputs into a ZIP file": Whether to bundle all output files into a single ZIP file</li>
                    </ul>
                  </li>
                </ul>
              </li>
              <li>
                <strong>Convert Images</strong>
                <ul>
                  <li>Click the "Convert Image" or "Convert All Images" button to start the conversion process.</li>
                  <li>The conversion results will be displayed in the "Conversion Results" section, showing file size and compression ratio.</li>
                  <li>Converted files will be automatically downloaded.</li>
                </ul>
              </li>
            </ol>

            <h3>Color Format Descriptions</h3>
            <ul>
              <li><strong>NVP (Alpha Channel)</strong>: A special format that extracts alpha channel information from images. Used for transparency masks.</li>
              <li><strong>Black & White (1-bit)</strong>: Each pixel is either black or white, using only 1 bit per pixel.</li>              
              <li><strong>32-bit Format (8888)</strong>: A 32-bit color format using 8 bits per channel (R, G, B, A).</li>
              <li><strong>16-bit Format (4444)</strong>: A 16-bit color format using 4 bits per channel (R, G, B, A).</li>
              <li><strong>16-bit Format (565)</strong>: A 16-bit color format using 5 bits for red, 6 bits for green, and 5 bits for blue.</li>
            </ul>

            <h3>Output File Formats</h3>
            <ul>
              <li><strong>Binary</strong>: Raw binary data, which can be directly loaded onto an embedded system.</li>
              <li>
                <strong>C Array</strong>: Output as a C language array, allowing easy inclusion in source code.
                <ul>
                  <li>Includes image data array</li>
                  <li>Contains image width, height, and format information</li>
                </ul>
              </li>
            </ul>

            <h3>Usage Tips</h3>
            <ol>
              <li>You can add multiple batches of images by clicking "Select Images" multiple times.</li>
              <li>To remove a specific image, click the delete button next to that image.</li>
              <li>To start fresh with a new set of images, use the "Clear" button to remove all selected images.</li>
              <li>Select a color format that meets the requirements of your embedded device.</li>
              <li>If memory is limited, using a 16-bit format (4444 or 565) can help save space.</li>
              <li>For maximum memory savings, use the Black & White format which uses only 1 bit per pixel.</li>
              <li>When converting multiple images at once, using the ZIP option can be convenient.</li>
              <li>Check the compression ratio in the conversion results to select the optimal format.</li>
            </ol>

            <p>With this tool, you can easily convert images into formats optimized for embedded devices.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Optional: Import JSZip if using ZIP output
import JSZip from 'jszip';

export default {
  name: 'Novatool NovaTool',

  created() {
    document.title = this.$route.name // 현재 라우트 이름으로 페이지 타이틀을 설정합니다.
  },
  data() {
    return {
      selectedFiles: [],
      imagePreview: '',
      imageWidth: 0,
      imageHeight: 0,
      format: 'rgba8888',
      outputType: 'binary',
      zipOutput: true,
      isConverting: false,
      conversions: [],
      showHelpModal: false, // 도움말 모달 표시 여부를 위한 상태
      bwThreshold: 128, // Black & White 변환을 위한 임계값 (0-255)
      nvpAlphaMode: 'alpha', // NVP 알파 모드: 'alpha' 또는 'luminance'
      nvpInvert: false // 휘도 값 반전 여부
    }
  },
  computed: {
    getLegendClass() {
      if (this.format === 'bw' || this.format === 'bw4bit' || this.format === 'bw8bit') {
        return 'bw';
      } else if (this.format === 'nvp') {
        return 'nvp';
      }
      return this.format.split(/\d+/)[0];
    },
    getChannelOrder() {
      // 모든 그레이스케일 형식에 대한 처리
      // NVP 형식 처리
      if (this.format === 'nvp') {
        return ['ALPHA'];
      }


      if (this.format === 'bw') {
        return ['B&W'];
      } else if (this.format === 'bw4bit' || this.format === 'bw8bit') {
        return ['GRAY'];
      }

      const format = this.format.toLowerCase();
      if (format.includes('565')) {
        return format.startsWith('rgb') ? ['R', 'G', 'B'] : ['B', 'G', 'R'];
      }
      return format.split(/\d+/)[0].toUpperCase().split('');
    },

    getFormatDescription() {
      if (this.format === 'nvp') {
        return 'Alpha Channel Only (8-bit)';
      } else if (this.format === 'bw') {
        return '1-bit (Black & White)';
      } else if (this.format === 'bw4bit') {
        return '4-bit (Grayscale)';
      } else if (this.format === 'bw8bit') {
        return '8-bit (Grayscale)';
      } else if (this.format.includes('8888')) {
        return '32-bit (Each channel 8-bit)';
      } else if (this.format.includes('4444')) {
        return '16-bit (Each channel 4-bit)';
      } else {
        return '16-bit (R:5, G:6, B:5)';
      }
    }
  },
  // metaInfo: {
  //   title: this.$route.name,
  //   titleTemplate: '%s | novatool-304.github.io', // title 뒤에 붙일 공통된 이름 (주로 사이트이름) 
  //   htmlAttrs: {
  //     lang: window.getApp.$i18n.locale // i18n 사용하지 않는 경우 직접 입력
  //   },

  //   meta: [
  //     { charset: 'utf-8'},
  //     // SEO setting
  //     { name: 'description', content: 'Convert the image into an embedded device form', vmid: 'description'},
  //     { name: 'keywords', content: 'image converter, image to binary, image to c array', vmid: 'keywords'},
  //     { name: 'author', content: 'wingslee', vmid: 'author'},
  //     // SNS 용
  //     {
  //       property: 'og:title',
  //       content: this.$route.name,
  //       template: chunk => `${chunk} | novatool-304.github.io`,
  //       vmid: 'og:title'

  //     },
  //     // mobile
  //     { name: 'viewport', content: 'width=device-width, initial-scale=1'}
  //   ]

  // },
  methods: {
    // 파일 삭제 메서드
    removeFile(index) {
      // 선택된 파일 배열에서 해당 인덱스의 파일 제거
      this.selectedFiles.splice(index, 1);
      
      // 만약 모든 파일이 삭제되었다면 미리보기도 초기화
      if (this.selectedFiles.length === 0) {
        this.imagePreview = '';
        this.imageWidth = 0;
        this.imageHeight = 0;
      } 
      // 삭제 후 남은 파일이 있고, 현재 미리보기 중이던 파일이 삭제된 경우 첫 번째 파일 미리보기
      else {
        this.loadImagePreview(this.selectedFiles[0]);
      }
    },
    onFilesSelected(event) {
      const files = event.target.files;
      if (!files || files.length === 0) return;
      
      // 기존 코드에서 변경: 파일 초기화 대신 추가
      // this.selectedFiles = Array.from(files);
      
      // 새로 선택된 파일들을 기존 배열에 추가
      const newFiles = Array.from(files);
      this.selectedFiles = [...this.selectedFiles, ...newFiles];
      
      // 변환 결과 초기화 (새 파일이 추가되었으므로)
      this.conversions.splice(0, this.conversions.length);
      
      // 새로 추가된 첫 번째 파일 미리보기 (기존 파일이 없었을 경우)
      if (this.imagePreview === '' && this.selectedFiles.length > 0) {
        this.loadImagePreview(this.selectedFiles[0]);
      }
    },

    clearAllFiles() {
      this.selectedFiles = [];
      this.imagePreview = '';
      this.imageWidth = 0;
      this.imageHeight = 0;
      this.conversions = [];
      
      // 파일 입력 필드 초기화 (다음 선택 시 같은 파일 선택 가능하도록)
      this.$refs.fileInput.value = '';
    },
    
    loadImagePreview(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.imagePreview = e.target.result;
        this.loadImageDimensions(e.target.result);
      };
      reader.readAsDataURL(file);
    },
    
    loadImageDimensions(src) {
      const img = new Image();
      img.onload = () => {
        this.imageWidth = img.width;
        this.imageHeight = img.height;
      };
      img.src = src;
    },
    
    async convertAllImages() {
      if (this.selectedFiles.length === 0 || this.isConverting) return;

      this.$gtag.event('button_click', {
        event_category: 'interaction',
        event_label: 'CovertAllImages',
      }); // Google Analytics 이벤트 전송
      
      this.isConverting = true;
      this.conversions = [];
      
      try {
        if((this.selectedFiles.length == 1) || (this.zipOutput == false))        
        {
          for (const file of this.selectedFiles) {
            await this.processFile(file);
          }
        }
        else
        {
          await this.convertAndZip();
        }
       
      } catch (error) {
        console.error('Conversion error:', error);
        alert('An error occurred during conversion.');
      } finally {
        this.isConverting = false;
      }
    },
    
    async convertAndZip() {
      // Requires JSZip library
      // If JSZip is not imported, we can fall back to individual downloads
      if (typeof JSZip === 'undefined') {
        alert('ZIP functionality requires JSZip library. Processing files individually.');
        for (const file of this.selectedFiles) {
          await this.processFile(file);
        }
        return;
      }
      
      const zip = new JSZip(); 
      const folder = zip.folder("converted_images");
      
      for (const file of this.selectedFiles) {
        const result = await this.convertFile(file);
        
        if (result) {
          const { data, fileName, size, compressionRatio } = result;
          
          this.conversions.push({
            fileName,
            size: (size / 1024).toFixed(2),
            compressionRatio
          });
          
          // Add file to ZIP
          if (this.outputType === 'binary') {
            const extension = this.format === 'nvp' ? '.nvp' : '.bin';
            folder.file(fileName + extension, data);
          } else {
            folder.file(fileName + '.c', this.generateCCode(data, file.name, data.length));
          }
        }
      }
      
      // Generate ZIP file
      const content = await zip.generateAsync({type: 'blob'});
      const url = URL.createObjectURL(content);
      const a = document.createElement('a');
      a.href = url;
      a.download = `converted_images_${this.format}.zip`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
    
    async processFile(file) {
      const result = await this.convertFile(file);
      
      if (result) {
        const { data, fileName, size, compressionRatio } = result;
        
        this.conversions.push({
          fileName,
          size: (size / 1024).toFixed(2),
          compressionRatio
        });
        
        // Download individual file
        if (this.outputType === 'binary') {
          this.downloadBinary(data, fileName);
        } else {
          this.downloadCArray(data, fileName, file.name);
        }
      }
    },
    
    async convertFile(file) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          const img = new Image();
          img.onload = () => {
            const canvas = document.createElement('canvas');
            canvas.width = img.width;
            canvas.height = img.height;
            
            const ctx = canvas.getContext('2d');
            ctx.drawImage(img, 0, 0);
            
            const imageData = ctx.getImageData(0, 0, img.width, img.height).data;
            
            let result;
            
            // 형식별 변환 처리
            if (this.format === 'nvp') {
              result = this.convertToNvpFormat(imageData, img.width, img.height);
            } else if (this.format === 'bw') {
              result = this.convertToBWFormat(imageData, '1bit');
            } else if (this.format === 'bw4bit') {
              result = this.convertToBWFormat(imageData, '4bit');
            } else if (this.format === 'bw8bit') {
              result = this.convertToBWFormat(imageData, '8bit');
            } else if (this.format.endsWith('8888')) {
              result = this.convertTo8888Format(imageData, this.format);
            } else if (this.format.endsWith('4444')) {
              result = this.convertTo4444Format(imageData, this.format);
            } else if (this.format.endsWith('565')) {
              result = this.convertTo565Format(imageData, this.format);
            }
            
            const originalSize = imageData.length;
            const compressionRatio = ((result.length / originalSize) * 100).toFixed(1);
            
            resolve({
              data: result,
              fileName: this.getBaseFileName(file.name),
              width: img.width,
              height: img.height,
              size: result.length,
              compressionRatio
            });
          };
          img.src = e.target.result;
        };
        reader.readAsDataURL(file);
      });
    },
    
    getBaseFileName(name) {
      return name.split('.').slice(0, -1).join('.');
    },

    // NVP 포맷 변환 함수 추가
    convertToNvpFormat(imageData, width, height) {
      // NVP 파일 형식: 2바이트 너비 + 2바이트 높이 + 픽셀당 1바이트 알파값
      const headerSize = 4; // 2바이트 너비 + 2바이트 높이
      const pixelCount = width * height;
      const resultLength = headerSize + pixelCount;
      
      const result = new Uint8Array(resultLength);
      
      // 헤더 정보 설정 (리틀 엔디안)
      result[0] = width & 0xFF;
      result[1] = (width >> 8) & 0xFF;
      result[2] = height & 0xFF;
      result[3] = (height >> 8) & 0xFF;
      
      // 알파 채널 또는 휘도 값 추출
      for (let i = 0; i < pixelCount; i++) {
        let alphaValue;
        
        if (this.nvpAlphaMode === 'alpha') {
          // 이미지의 알파 채널 사용
          alphaValue = imageData[i * 4 + 3]; // A 채널
        } else {
          // 휘도 값 계산 (RGB 평균)
          const r = imageData[i * 4];
          const g = imageData[i * 4 + 1];
          const b = imageData[i * 4 + 2];
          alphaValue = Math.floor((r + g + b) / 3);
          
          // 휘도 값 반전 옵션
          if (this.nvpInvert) {
            alphaValue = 255 - alphaValue;
          }
        }
        
        // 알파 값 저장
        result[headerSize + i] = alphaValue;
      }
      
      return result;
    },

    convertToBWFormat(imageData, bit_len) {
      const pixelCount = imageData.length / 4;
      let resultLength;
      
      // 비트 길이에 따라 결과 배열 크기 계산
      switch (bit_len) {
        case '1bit':
          resultLength = Math.ceil(pixelCount / 8); // 8픽셀당 1바이트
          break;
        case '4bit':
          resultLength = Math.ceil(pixelCount / 2); // 2픽셀당 1바이트
          break;
        case '8bit':
          resultLength = pixelCount; // 1픽셀당 1바이트
          break;
        default:
          resultLength = Math.ceil(pixelCount / 8);
      }
      
      const result = new Uint8Array(resultLength);
      
      // 초기화
      for (let i = 0; i < resultLength; i++) {
        result[i] = 0;
      }
      
      switch (bit_len) {
        case '1bit':
          for (let i = 0; i < pixelCount; i++) {
            const r = imageData[i * 4];
            const g = imageData[i * 4 + 1];
            const b = imageData[i * 4 + 2];
            
            // Calculate grayscale value (simple average)
            const grayscale = Math.floor((r + g + b) / 3);
            
            
            // If grayscale is above threshold, set the corresponding bit to 1
            if (grayscale >= this.bwThreshold) {
              const byteIndex = Math.floor(i / 8);
              const bitPosition = i % 8;
              result[byteIndex] |= (1 << (7 - bitPosition)); // Set the bit (MSB first)
            }
          }
          break;

        case '4bit':
          for (let i = 0; i < pixelCount; i++) {
            const r = imageData[i * 4];
            const g = imageData[i * 4 + 1];
            const b = imageData[i * 4 + 2];

            // Calculate grayscale value (simple average)
            const grayscale = Math.floor((r + g + b) / 3);

            // 4비트로 변환 (0-15)
            const value4bit = Math.floor(grayscale / 16);
            
            // 2픽셀당 1바이트에 저장 (첫 픽셀은 상위 4비트, 두번째 픽셀은 하위 4비트)
            const byteIndex = Math.floor(i / 2);
            if (i % 2 === 0) {
              // 짝수 인덱스: 상위 4비트
              result[byteIndex] |= (value4bit << 4);
            } else {
              // 홀수 인덱스: 하위 4비트
              result[byteIndex] |= value4bit;
            }
          }
          break;

        case '8bit':
          for (let i = 0; i < pixelCount; i++) {
            const r = imageData[i * 4];
            const g = imageData[i * 4 + 1];
            const b = imageData[i * 4 + 2];

            // Calculate grayscale value (simple average)
            const grayscale = Math.floor((r + g + b) / 3);
            result[i] |= grayscale; // Set the bit (MSB first)
          }
          break;
        } 
      
      return result;
    },
    
    convertTo8888Format(imageData, format) {
      const length = imageData.length / 4;
      const result = new Uint8Array(imageData.length);
      
      for (let i = 0; i < length; i++) {
        const r = imageData[i * 4];     // Red
        const g = imageData[i * 4 + 1]; // Green
        const b = imageData[i * 4 + 2]; // Blue
        const a = imageData[i * 4 + 3]; // Alpha
        
        const pixelOffset = i * 4;
        
        // 포맷별 바이트 순서 배치
        switch (format) {
          case 'rgba8888':
            result[pixelOffset] = r;
            result[pixelOffset + 1] = g;
            result[pixelOffset + 2] = b;
            result[pixelOffset + 3] = a;
            break;
          case 'argb8888':
            result[pixelOffset] = a;
            result[pixelOffset + 1] = r;
            result[pixelOffset + 2] = g;
            result[pixelOffset + 3] = b;
            break;
          case 'bgra8888':
            result[pixelOffset] = b;
            result[pixelOffset + 1] = g;
            result[pixelOffset + 2] = r;
            result[pixelOffset + 3] = a;
            break;
          case 'abgr8888':
            result[pixelOffset] = a;
            result[pixelOffset + 1] = b;
            result[pixelOffset + 2] = g;
            result[pixelOffset + 3] = r;
            break;
        }
      }
      
      return result;
    },
    
    convertTo4444Format(imageData, format) {
      const length = imageData.length / 4;
      const result = new Uint8Array(length * 2); // 각 픽셀당 2바이트
      
      for (let i = 0; i < length; i++) {
        const r = Math.floor(imageData[i * 4] / 16);       // Red: 0-15
        const g = Math.floor(imageData[i * 4 + 1] / 16);   // Green: 0-15
        const b = Math.floor(imageData[i * 4 + 2] / 16);   // Blue: 0-15
        const a = Math.floor(imageData[i * 4 + 3] / 16);   // Alpha: 0-15
        
        const pixelOffset = i * 2;
        
        // 포맷별 4비트 값 패킹
        switch (format) {
          case 'rgba4444':
            result[pixelOffset] = (r << 4) | g;
            result[pixelOffset + 1] = (b << 4) | a;
            break;
          case 'argb4444':
            result[pixelOffset] = (a << 4) | r;
            result[pixelOffset + 1] = (g << 4) | b;
            break;
          case 'bgra4444':
            result[pixelOffset] = (b << 4) | g;
            result[pixelOffset + 1] = (r << 4) | a;
            break;
          case 'abgr4444':
            result[pixelOffset] = (a << 4) | b;
            result[pixelOffset + 1] = (g << 4) | r;
            break;
        }
      }
      
      return result;
    },
    
    convertTo565Format(imageData, format) {
      const length = imageData.length / 4;
      const result = new Uint8Array(length * 2); // 각 픽셀당 2바이트
      
      for (let i = 0; i < length; i++) {
        // 원본 0-255 값을 비트 수에 맞게 변환
        // R: 5비트 (0-31), G: 6비트 (0-63), B: 5비트 (0-31)
        const r = Math.floor(imageData[i * 4] / 255 * 31);      // Red: 0-31
        const g = Math.floor(imageData[i * 4 + 1] / 255 * 63);  // Green: 0-63
        const b = Math.floor(imageData[i * 4 + 2] / 255 * 31);  // Blue: 0-31
        
        const pixelOffset = i * 2;
        
        // RGB565는 16비트 색상 형식으로 다음과 같이 구성됩니다.
        // RRRRRGGGGGGBBBBB (R: 5비트, G: 6비트, B: 5비트)
        let pixel16bit;
        
        if (format === 'rgb565') {
          // RGB565: R(5비트) | G(6비트) | B(5비트)
          pixel16bit = (r << 11) | (g << 5) | b;
        } else { // BGR565
          // BGR565: B(5비트) | G(6비트) | R(5비트)
          pixel16bit = (b << 11) | (g << 5) | r;
        }
        
        // 16비트 값을 2개의 바이트로 저장 (리틀 엔디안)
        result[pixelOffset] = pixel16bit & 0xFF;         // 하위 8비트
        result[pixelOffset + 1] = (pixel16bit >> 8) & 0xFF; // 상위 8비트
      }
      
      return result;
    },
    
    downloadBinary(data, fileName) {
      const blob = new Blob([data], { type: 'application/octet-stream' });
      const url = URL.createObjectURL(blob);
      
      // NVP 형식인 경우 .nvp 확장자 사용, 그 외에는 .bin 사용
      const extension = this.format === 'nvp' ? '.nvp' : '.bin';

      const a = document.createElement('a');
      a.href = url;
      a.download = `${fileName}_${this.format}${extension}`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
    
    downloadCArray(data, fileName, originalFileName) {
      const cCode = this.generateCCode(data, originalFileName, data.length);
      
      const blob = new Blob([cCode], { type: 'text/plain' });
      const url = URL.createObjectURL(blob);
      
      const a = document.createElement('a');
      a.href = url;
      a.download = `${fileName}_${this.format}.c`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
    
    generateCCode(data, originalFileName, dataLength) {
      let cCode = `// ${this.format} 이미지 데이터 - ${originalFileName}\n`;
      
      // 각 형식에 대한 설명 추가
      if (this.format.includes('rgba')) {
        cCode += '// Byte order: Red, Green, Blue, Alpha\n';
      } else if (this.format.includes('argb')) {
        cCode += '// Byte order: Alpha, Red, Green, Blue\n';
      } else if (this.format.includes('bgra')) {
        cCode += '// Byte order: Blue, Green, Red, Alpha\n';
      } else if (this.format.includes('abgr')) {
        cCode += '// Byte order: Alpha, Blue, Green, Red\n';
      } else if (this.format === 'rgb565') {
        cCode += '// RGB565 Format: 16-bit (RRRRRGGGGGGBBBBB)\n';
        cCode += '// Little-endian byte order: Lower 8-bit, Upper 8-bit\n';
      } else if (this.format === 'bgr565') {
        cCode += '// BGR565 Format: 16-bit (BBBBBGGGGGGRRRRR)\n';
        cCode += '// Little-endian byte order: Lower 8-bit, Upper 8-bit\n';
      }
      
      // 4444 형식인 경우 패킹 설명 추가
      if (this.format.includes('4444')) {
        cCode += '// 4-bit packing format: Each byte contains two 4-bit color values\n';
      }
      
      const safeFileName = this.getSafeVariableName(originalFileName);
      cCode += `const unsigned char ${safeFileName}_data[${dataLength}] = {\n`;
      
      const bytesPerLine = 12;
      for (let i = 0; i < dataLength; i += bytesPerLine) {
        cCode += '    ';
        for (let j = 0; j < bytesPerLine && i + j < dataLength; j++) {
          cCode += `0x${data[i + j].toString(16).padStart(2, '0')}`;
          if (i + j < dataLength - 1) {
            cCode += ', ';
          }
        }
        cCode += '\n';
      }
      
      cCode += '};\n\n';
      cCode += `const int ${safeFileName}_width = ${this.imageWidth};\n`;
      cCode += `const int ${safeFileName}_height = ${this.imageHeight};\n`;
      cCode += `const char* ${safeFileName}_format = "${this.format}";\n`;
      
      return cCode;
    },
    
    getSafeVariableName(fileName) {
      // Remove extension and replace non-alphanumeric characters with underscore
      return fileName
        .split('.')[0]
        .replace(/[^a-zA-Z0-9]/g, '_')
        .replace(/^[0-9]/, '_$&'); // Ensure it doesn't start with a number
    },
    
    formatFileSize(size) {
      if (size < 1024) {
        return size + ' B';
      } else if (size < 1024 * 1024) {
        return (size / 1024).toFixed(2) + ' KB';
      } else {
        return (size / (1024 * 1024)).toFixed(2) + ' MB';
      }
    },

    getChannelStyle(channel) {
      const styles = {
        'R': { 
          background: '#ff0000',
          color: '#ffffff', // 흰색 텍스트
          border: '1px solid #222',
          textShadow: '0px 0px 2px #000000' // 검은색 그림자          
        },
        'G': { 
          background: '#00ff00',
          color: '#000000', // 검은색 텍스트
          border: '1px solid #222',
          textShadow: '0px 0px 2px #ffffff' // 흰색 그림자
        },
        'B': { 
          background: '#0000ff',
          color: '#ffffff', // 흰색 텍스트
          border: '1px solid #222',
          textShadow: '0px 0px 2px #000000' // 검은색 그림자
        },
        'A': { 
          background: '#ffffff',
          color: '#000000', // 검은색 텍스트
          textShadow: '0px 0px 2px #ffffff', // 흰색 그림자
          border: '1px solid #222'
        },
        'ALPHA': {
          background: 'linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,1))',
          color: '#ffffff', // 흰색 텍스트
          border: '1px solid #222',
          textShadow: '0px 0px 2px #000000', // 검은색 그림자
          padding: '3px'
        },
        'B&W': {
          background: 'linear-gradient(to right, #000000 50%, #ffffff 50%)',
          color: '#ffffff', // 흰색 텍스트
          border: '1px solid #222',
          textShadow: '-1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000' // 검은색 외곽선
          ,padding: '3px'
          // fontWeight: 'bold'
        },
        'GRAY': {
          background: 'linear-gradient(to right, #000000, #ffffff)',
          color: '#ffffff', // 흰색 텍스트
          border: '1px solid #222',
          textShadow: '-1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000' // 검은색 외곽선
          ,padding: '3px'
          //fontWeight: 'bold'
        }
      };
      return styles[channel] || {};
    }
  }
}
